html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.body-fixed {
  overflow: hidden;
}

a {
  font-size: 2rem;
  color: #000;
  text-decoration: none;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 3.6rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.4rem;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 2;
  margin: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.header {
  background-color: #4cbbb3;
}
@media (min-width: 768px) {
  .header {
    position: sticky;
    top: 0;
    z-index: 10;
  }
}
.header h1 {
  color: #fff;
  text-align: center;
  margin: 0;
}
.header .content-header {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .header .content-header {
    flex-direction: row;
    justify-content: space-between;
    column-gap: 2rem;
    row-gap: unset;
  }
}

.main-nav {
  display: flex;
  column-gap: 2rem;
}
.main-nav a {
  color: #fff;
  font-size: 2.4rem;
}
.main-nav a:hover {
  color: #f53756;
}

.video {
  position: relative;
  max-height: 88vh;
  overflow: hidden;
}
.video .overlay {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(165, 3, 255);
  background: linear-gradient(270deg, rgba(165, 3, 255, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.video-content {
  z-index: 1;
}
.video-content h2 {
  color: #fff;
  font-size: 7rem;
  margin: 0;
}
.video-content p {
  background-color: #f53756;
  display: inline-block;
  padding: 1rem 2rem;
  color: #fff;
  font-weight: bold;
  font-size: 2.4rem;
  border-radius: 1rem;
}

.festival-about {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .festival-about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.festival-about .image img {
  border-radius: 1rem;
  box-shadow: 1px 1px 15px 3px rgba(0, 0, 0, 0.75);
}

.festival-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 2.5rem;
}
@media (min-width: 768px) {
  .festival-content {
    margin-top: 0;
  }
}
.festival-content h2 {
  margin-bottom: 2rem;
}
.festival-content .fecha {
  color: #f53756;
  font-weight: bold;
}

.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #fff;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 3rem 0;
}

@media (min-width: 768px) {
  .scene-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.scene-content .name-scene {
  color: #000;
}
.scene-content .name-scene::after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}
.scene-content .scene {
  padding-top: 2rem;
  border-radius: 2rem;
}
.scene-content .scene:nth-child(odd) {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .scene-content .scene:nth-child(odd) {
    margin-bottom: 0;
  }
}

.edm .name-scene::after {
  background-image: url(../img/icono_edm.png);
}

.rock .name-scene::after {
  background-image: url(../img/icono_rock.png);
}

.calendar li {
  padding: 2.5rem;
  font-weight: bold;
}
.calendar li span {
  font-weight: normal;
}

.bg-green {
  background-color: #4cbbb3;
}
.bg-green li:nth-child(odd) {
  background-color: #3a9a93;
}

.bg-yellow {
  background-color: #fef460;
}
.bg-yellow li:nth-child(odd) {
  background-color: #fef02d;
}

.galery {
  padding: 5rem 0;
}
.galery h3 {
  text-align: center;
}

.galery-images {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .galery-images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}
.galery-images img {
  border-radius: 0.5rem;
  cursor: pointer;
  box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.75);
}
.galery-images img:hover {
  box-shadow: 3px 3px 5px 1px #f53756;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 5rem;
  background-color: rgba(20, 20, 20, 0.5);
  backdrop-filter: blur(10px);
}
.overlay picture {
  display: block;
  max-width: 40rem;
}
@media (min-width: 768px) {
  .overlay picture {
    max-width: 80rem;
  }
}
.overlay img {
  border-radius: 2rem;
}

.btn-close {
  background-color: #fff;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  font-weight: bold;
  cursor: pointer;
  border: none;
}

.tickets {
  background-color: #4cbbb3;
  padding: 5rem 0;
}
.tickets h3 {
  text-align: center;
  color: #fff;
  font-size: 2.8rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .tickets .tickets-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pass-header {
  background-color: #f53756;
}
.pass-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pass-body {
  background-color: #fff;
}
.pass-body li {
  padding: 1.5rem 2rem;
  border-bottom: 1px solid #b1b1b1;
}
.pass-footer {
  background-color: #752f97;
}
.pass-footer p {
  font-size: 2.4rem;
  font-weight: bold;
}
.pass-header p, .pass-footer p {
  text-align: center;
  color: #fff;
}
.pass-name {
  margin: 2rem 0;
  text-align: center;
  font-size: 2.4rem;
  color: #fff;
}

.site-footer {
  background-color: #f53756;
  padding: 1rem;
}
.site-footer p {
  text-align: center;
  color: #fff;
}